import React, {Component} from 'react'
import Book from './Book'

export default class AddForm extends Component {

    constructor(props) {
        super(props)

        this.state = {
            books: [],

            title: '',
            author: '',
            year: '',
        }
        this.textTitleHandler = this.textTitleHandler.bind(this)
        this.textAuthorHandler = this.textAuthorHandler.bind(this)
        this.textYearHandler = this.textYearHandler.bind(this)
        this.submitHandler = this.submitHandler.bind(this)

    }

    textTitleHandler(event) {
        this.setState({title: event.target.value})
    }

    textAuthorHandler(event) {
        this.setState({author: event.target.value})
    }

    textYearHandler(event) {
        this.setState({year: event.target.value})
    }

    submitHandler(event) {
        event.preventDefault()
        if (this.state.title.length >0 && this.state.author.length >0 && this.state.year.length >0) {
            this.setState((state)=>{
                let newBooks= state.books;
                newBooks.push({title: this.state.title,
                    author: this.state.author,
                    year: this.state.year
                ,id :newBooks.length})

                return{
                    books:newBooks
                }
            })
            this.setState({
                title: '',
                author: '',
                year: ''
            })
        }
    }

    render() {
        return (
            <>
                <form id="book-form" autocomplete="off" onSubmit={this.submitHandler}>
                    <div className="form-group">
                        <label for="title">Title</label>
                        <input type="text" id="title" value={this.state.title} onChange={this.textTitleHandler}
                               className="form-control"/>
                    </div>

                    <div className="form-group">
                        <label for="author">Author</label>
                        <input type="text" id="author" value={this.state.author} onChange={this.textAuthorHandler}
                               className="form-control"/>
                    </div>

                    <div className="form-group">
                        <label for="year">Year</label>
                        <input type="text" id="year" value={this.state.year} onChange={this.textYearHandler}
                               className="form-control"/>
                    </div>
                    <input type="submit" value="Add Book" className="btn btn-warning btn-block add-btn"/>
                </form>
                <table class="table table-striped mt-5 text-center">
                    <thead>
                    <tr>
                        <th>Title</th>
                        <th>Author</th>
                        <th>Year</th>
                    </tr>
                    </thead>
                    <tbody id="book-list">
                    {this.state.books.length >0 && this.state.books.map(book=><Book key={book.id} Title={book.title} Author={book.author} Year={book.year}/>)}
                    </tbody>
                </table>


            </>
        )
    }
}
